import animations from './animations';

const mainSlider = (wrapperSelector, innerSelector, changeInterval) => {
  const inner = document.querySelector(innerSelector),
        wrapper = document.querySelector(wrapperSelector),
        slides = inner.children,
        width = Math.round(window.getComputedStyle(slides[0]).width.replace(/px/g, ""));

  function fadeInContent(contentSelector, animationName) {
    document.querySelectorAll(contentSelector).forEach((item, i) => {
      item.classList.remove(animationName);
    });

    inner.firstElementChild.querySelector(contentSelector).classList.add(animationName);
  }

  fadeInContent('.promo__content', 'animate__fadeInUp');

  inner.style.display = 'flex';
  inner.style.transition = '1.5s all';
  inner.style.width = '300%';
  wrapper.style.overflow = 'hidden';
  slides.forEach(slide => {
    slide.style.width = `${width}px`;
  });

  if (changeInterval) {
    setInterval(() => {
      inner.style.transform = `translateX(-${width}px)`;
    }, changeInterval);
  } else {

  }

  inner.addEventListener('transitionend', () => {
    inner.appendChild(inner.firstElementChild);
    inner.style.transition = 'none';
    inner.style.transform = `translateX(0)`;

    setTimeout(function() {
      inner.style.transition = '1.5s all';
    });

    fadeInContent('.promo__content', 'animate__fadeInUp');
  });
};

export default mainSlider;
